<script>
    export default {
        name: "Main"
    }
</script>

<style scoped>
  #main{
    height:100vh;
    display: flex;
    flex-direction: column;
    /*display: auto;*/
  }
  footer{
    height:50px;
    width: 100vw;
    background:#aeaeae;
    text-align: center;
    font-size: 12px;
    color: #FFFFFF;
    bottom: 0;
    position: fixed;
  }
  span{
    float:left;
    height:100vh;
    width: 25%;
  }
  ul{
    margin-top: 10px;
    list-style: none;
  }
  section{
    flex: 1;
  }
  .router-link-active{
    color: #ff0000;
  }
</style>



