<template>
  <div>
    <header>
      <img class="header-img" src="https://www.uaf.com.cn/tpl_res/images/logo.png"/>
    </header>
    <section>
      <mt-swipe :auto="4000" class="swipe">
        <mt-swipe-item class="lunbotu"><img @click="about()" src="https://www.uaf.com.cn/tpl_res/upload/cms/www/201708/011504400jyv.jpg"/> </mt-swipe-item>
        <mt-swipe-item class="lunbotu"><img @click="chanpin()" src="https://www.uaf.com.cn/tpl_res/upload/cms/www/201708/010955061e24.jpg"/> </mt-swipe-item>
        <mt-swipe-item class="lunbotu"><img @click="jiang()" src="https://www.uaf.com.cn/tpl_res/upload/cms/www/201804/10150747j00d.jpg"/> </mt-swipe-item>
      </mt-swipe>
      <p class="gonggao">暂无公告</p>
      <div class="daohang">
        <router-link to="/newer" tag="span">
          <p class="tu iconfont icon-xinyuangongzhiyin"></p>
          <p class="biao">新手指引</p>
          <p class="xiang">申请快速 手续便捷</p>
        </router-link>

        <span @click="jiang()">
          <p class="tu iconfont icon-tuijianyoujiang"></p>
          <p class="biao">推荐有奖</p>
          <p class="xiang">推荐好友 奖金多多</p>
        </span>

        <router-link to="/chanpin" tag="span">
          <p class="tu iconfont icon-wenzhang"></p>
          <p class="biao">产品介绍</p>
          <p class="xiang">各种人群 量身定制</p>
        </router-link>
      </div>
      <div class="dimg">
        <img src="static/aa.jpg"/>
      </div>
      <ul>
        <li>
          <p class="j iconfont icon-changjianwenti"></p>
          <p class="da">进度查询</p>
          <p class="xiao">查询申请进度</p>
        </li>
        <li class="liii">
          <p class="j iconfont icon-tubiaolunkuohua09"></p>
          <p class="da">我要还款</p>
          <p class="xiao">进行还款操作</p>
        </li>
      </ul>
    </section>

  </div>

</template>


<script>
  export default {
    name: "Shouye",
    methods:{
      jiang(){
          this.$router.push("/tuijian")
      },
      about(){
        this.$router.push("/about")
      },
      chanpin(){
        this.$router.push("/chanpin")
      }
    }
  }


</script>

<style scoped>
  header{
    height:45px;
    top: 0;
    width:100vw;
    position: fixed;
    background: #FFFFFF;
    z-index: 10;
  }
  .header-img{
    height:35px;
    width:163px;
    display: block;
    margin-left:6px;
    margin-top: 8px;
  }
  section{
    flex: 1;
    position: absolute;
    top:45px;
  }
  .swipe{
    height:162px;
    width:100vw;
  }
  .lunbotu img{
    height:100%;
    width:100%;
  }
  .gonggao{
    height:25px;
    width:100vw;
    border-bottom: 1px solid #f6f8f8;
    font-size: 14px;
    line-height: 25px;
  }
  .daohang{
    height:90px;
    width:100vw;
    border-bottom: 1px solid #f6f8f8;
  }
  span{
    width:33.33%;
    float:left;
    text-align: center;
  }
  .tu{
    margin-top: 10px;
    color: red;
    font-size: 36px;
  }
  .biao{
    font-size: 14px;
  }
  .xiang{
    font-size:8px;
  }
  .dimg{
    height:132px;
    width:100vw;
    margin-top:2px;
    border-bottom: 1px solid #f6f8f8;
  }
  .dimg img{
    height:128px;
    width:100%;
  }
  ul{
    height:60px;
    width:100vw;
    list-style: none;
  }
  li{
    float:left;
    width:49%;
    text-align: center;
    height:100%;
    border-bottom:1px solid #f6f8f8;
    border-right:1px solid #f6f8f8;
  }
  .liii{
    border-right:none;
  }
  .j{
    float:left;
    font-size: 32px;
    color: #404040;
    margin-top:10px;
    margin-left: 35px;
  }
  .da{
    font-size: 14px;
    margin-top:10px;
  }
  .xiao{
    font-size: 8px;
  }
</style>



