<template>
  <div>
    <header>
      <span class="fan iconfont icon-left" @click="tap()"></span>
      <p class="biaoti">邀请好友</p>
      <p class="guan"></p>
    </header>
    <section>
      <img src="static/yaoqing1.jpg"/>
    </section>

  </div>

</template>

<script>
    export default {
        name: "Yaoqing",
      methods:{
          tap(){
            this.$router.push("/tuijian")
          }
      }
    }
</script>

<style scoped>
  header{
    height:45px;
    line-height: 45px;
    font-size: 16px;
    color: red;
  }
  .fan{
    float: left;
    font-size: 28px;
    line-height: 45px;
    text-align: center;
    width:15%;
  }
  .biaoti{
    text-align: center;
    float: left;
    width:70%;
    font-weight: bold;
  }
  .guan{
    float: left;
    /*margin-right:3%;*/
    text-align: center;
    font-size: 17px;
    width:15%;
  }
  section{
    width:100vw;
    height:100vh;
    background: #f7f8f8;
    flex: 1;
  }
  section img{
    height:100%;
    width:100%;
  }
</style>



