<template>
  <div>
    <header>
      <span class="fan iconfont icon-left" @click="tap()"></span>
      <p class="biaoti">推荐好友</p>
      <p class="guan"></p>
    </header>
    <section>
      <div class="lists">
        <input class="tex" type="text" placeholder="请输入邀请的手机号" v-model="astr"/>
        <div class="c"><input name="aa" type="radio" checked="checked"/>确认推荐</div>
        <button class="bb" @click="a()"><p>邀请</p></button>
      </div>
    </section>
  </div>
</template>

<script>

  import axios from "axios";
    export default {
        name: "Shenfen",
      data(){
          return{
            astr:'',
          }
      },
      methods:{
        tap(){
          this.$router.push("/my")
        },
        a(){
          var _this = this;
          axios({
            method:"post",
            url:"http://localhost:3000/chanpin",
            params:{p:_this.astr,j:"200"}
          }).then(function (data) {
            if(data.data == 1){
              console.log(eval(data.data))
            }
          })
        }
      }


    }
</script>

<style scoped>
  header{
    height:45px;
    line-height: 45px;
    font-size: 16px;
    color: red;
    z-index: 10;
    background: #FFFFFF;
    position: fixed;
    top: 0;
    width: 100vw;
    border-bottom: 1px solid #c0c0c0;
  }
  .fan{
    float: left;
    /*margin-left: 3%;*/
    font-size: 28px;
    line-height: 45px;
    text-align: center;
    width:15%;
  }
  .biaoti{
    text-align: center;
    float: left;
    width:70%;
    font-weight: bold;
  }
  .guan{
    float: left;
    /*margin-right:3%;*/
    text-align: center;
    font-size: 17px;
    width:15%;
  }
  section{
    position: absolute;
    top: 45px;
    margin-top: 10px;
    flex:1;
    width:100vw;
    height:100vh;
  }
  .lists{
    list-style: none;
    width:85vw;
    height:100vh;
    margin:0 auto;
  }
  .lists .tex{
    height:50px;
    width:100%;
    /*display: block;*/
    border-bottom:1px solid #404040;
    margin-top:5px;
    background:#c0c0c0;
    border-radius: 7px;
    line-height: 50px;
    border: none;
    outline: none;
  }
  .c{
    height:50px;
    margin-top:20px;
  }
  .bb{
    background: red;
    opacity: 0.8;
    height:30px;
    width:50px;
    border: none;
    outline: none;
    border-radius: 10px;
    float:right;
  }
  .bb p{
    z-index: 100;
    /*color:orange;*/
    line-height: 20px;
    margin-bottom: 10px;
    text-align: center;
    line-height: 30px;
  }

</style>



