<template>
  <div>
    <header>
      <span class="fan iconfont icon-left" @click="tap()"></span>
      <p class="biaoti">账户</p>
      <p class="guan"></p>
    </header>
    <section>
      <div class="lists">
        <input class="tex" type="text" placeholder="请输入您要提现的金额" v-model="astr"/>
        <button class="bb" @click="a()"><p>确认提现</p></button>
      </div>
    </section>

  </div>

</template>

<script>
    export default {
        name: "Zhanghuanquan",
      data(){
          return{
            astr:''
          }
      },
      methods:{
          a(){
            var _this = this;
            axios({
              method:"post",
              url:"http://localhost:3000/chanpin",
              params:{tixian:_this.aster}
            }).then(function (data) {
              console.log(data);
            })
          },
          tap(){
            this.$router.push("/my")
          },
        tap1(){
            this.$router.push("/register")
        }
      }
    }
</script>

<style scoped>
  header{
    height:45px;
    line-height: 45px;
    font-size: 16px;
    color: red;
    z-index: 10;
    position: fixed;
    width: 100vw;
    text-align: center;
  }
  .fan{
    float: left;
    font-size: 28px;
    line-height: 45px;
    text-align: center;
    width:15%;
  }
  .biaoti{
    text-align: center;
    float: left;
    width:70%;
    font-weight: bold;
  }
  .guan{
    float: left;
    /*margin-right:3%;*/
    text-align: center;
    font-size: 17px;
    width:15%;
  }
  section{
    position: absolute;
    top: 45px;
    margin-top: 10px;
    flex:1;
    width:100vw;
    height:100vh;
  }
  .lists{
    list-style: none;
    width:85vw;
    height:100vh;
    margin:0 auto;
  }
  .lists .tex{
    height:50px;
    width:100%;
    /*display: block;*/
    border-bottom:1px solid #404040;
    margin-top:5px;
    background:#c0c0c0;
    border-radius: 7px;
    line-height: 50px;
    border: none;
    outline: none;
  }
  .c{
    height:50px;
    margin-top:20px;
  }
  .bb{
    background:#c0c0c0;
    opacity: 0.8;
    height:50px;
    width:150px;
    border: none;
    outline: none;
    border-radius: 10px;
    margin:0 auto;
    display: block;
    margin-top:100px;
  }
  .bb p{
    line-height: 50px;
    margin-bottom: 10px;
    text-align: center;
    line-height: 50px;
    font-size: 20px;
    color: orangered;
  }
</style>



