<template>
  <div>
    <header>
      <span class="fan iconfont icon-left" @click="tap()"></span>
      <p class="biaoti">奖金查询</p>
      <p class="guan"></p>
    </header>
    <section>
      <div class="user">
        <div>
          <p>累计推荐奖金</p>
          <span>￥ {{str}}</span>
        </div>
      </div>
      <div class="daohang">
        <div>
          <p>你推荐的好友</p>
          <span>{{people1}}人</span>
        </div>
        <div>
          <p>成功放款人数</p>
          <span>{{people2}}人</span>
        </div>
        <div class="three">
          <p>可使用推荐奖金</p>
          <span>￥{{money}}</span>
        </div>
      </div>
      <table>
        <tr>
          <th>被推荐人手机</th>
          <th>奖金（元）</th>
        </tr>
        <tr v-for="item in a">
          <td>{{item.p}}</td>
          <td>{{item.j}}</td>
        </tr>
      </table>
      <div class="cheng"></div>
    </section>

    <div class="tixian"><span @click="tixian()">全部提现</span></div>
  </div>

</template>

<script>

  import axios from "axios";
  import { MessageBox } from 'mint-ui';

    export default {
        name: "Chaxun",
      data:function(){
        return{
          str:"0.00",
          people1:"0",
          people2:"0",
          money:"0.00",
          a:[]
        }
      },

      methods:{
        tap(){
          this.$router.push("/tuijian")
        },
        tixian(){
          axios({
            method:"post",
            url:"http://localhost:3000/tixian",
            params:{callback:""}
          }).then(function (data) {

          });
          MessageBox({
            title: '提示',
            message: '您已经全部提现',
            showCancelButton: true
          });
          this.$router.go(0);
        }

      },

      mounted:function(){
         var _this = this;
          var num = 0;
          var x = 0;
          axios({
              method:"post",
              url:"http://localhost:3000/tuijian",
              params:{p:localStorage.getItem('num')}
          }).then(function (data) {
            _this.a = eval(data.data.info);
              for (var i = 0;i<eval(data.data.info).length;i++){
                num += eval(data.data.info)[i].j
              }
              _this.str = num;
              _this.people1 = eval(data.data.info).length;
              _this.people2 = _this.people1;
              _this.money = num;
          })


      }

    }
</script>

<style scoped>
  header{
    height:45px;
    width:100vw;
    position: fixed;
    z-index: 10;
    background: #FFFFFF;
    line-height: 45px;
    font-size: 16px;
    color: red;
  }
  .fan{
    float: left;
    font-size: 28px;
    line-height: 45px;
    text-align: center;
    width:15%;
  }
  .biaoti{
    text-align: center;
    float: left;
    width:70%;
    font-weight: bold;
  }
  .guan{
    float: left;
    text-align: center;
    font-size: 17px;
    width:15%;
  }
  section{
    position: absolute;
    top:45px;
    flex: 1;
    background: #f8f8f8;
  }
  .user{
    height:130px;
    width:100vw;
    background:#de2f21;
    text-align: center;
    color: #ffffff;
  }
  .user p{
    display: block;
    line-height:60px;
    font-size: 14px;
    text-align: center;
  }
  .user span{
    display: block;
    font-size: 20px;
    line-height:10px;
    text-align: center;
  }
  .daohang{
    height:96px;
    width:300px;
    background: #FFFFFF;
    margin:0 auto;
    position: relative;
    z-index: 10;
    top:-32px;
    border-radius:5px;
    box-shadow:0 2px 1px #ff7700;
  }
  .daohang div{
    text-align: center;
    width:32.8%;
    float: left;
    margin-top:15px;
    border-right:2px solid #c0c0c0 ;
  }
  .daohang .three{
    border:none;
  }
  .daohang p{
    height:40px;
    /*width:50px;*/
    color: #c0c0c0;
    font-size:8px;
    text-align: center;
    line-height: 40px;
    margin:0 auto;
  }
  .daohang span{
    color: red;
    font-size:12px;
  }
  table{
    background:#FFFFFF;
    width:100vw;
    text-align: center;
    font-size: 15px;
    color: #0f0f0f;
  }
  tr{
    height:100%;
    line-height: 40px;
    border-bottom: 1px solid #000000;
    font-weight: normal;
  }
  th{
    /*background: #FFFFFF;*/
  }
  .cheng{
    height:65px;
    width: 100vw;
  }
  .tixian{
    height:65px;
    width:100vw;
    background: #FFFFFF;
    line-height: 40px;
    position: fixed;
    bottom: 0;
    z-index: 10;
  }
  .tixian span{
    display: block;
    height:40px;
    width:90%;
    margin-left:5%;
    background: #de2f21;
    color: #FFFFFF;
    font-size: 16px;
    text-align: center;
    line-height: 40px;
  }
</style>



