import Vue from 'vue'
import Router from 'vue-router'
import Mint from "mint-ui"

//接收main暴露出来的组件
import Main from '../components/main.vue';


import Shouye from "../components/shouye.vue";
import Tuijian from "../components/tuijian.vue";
import My from "../components/my.vue";
import Gengduo from "../components/gengduo.vue";

//接收首页暴露的组件
import Newer from "../components/newer.vue";
import Chanpin from "../components/chanpin.vue";

import About from "../components/about.vue"

//接收推荐暴露的组件
import Chaxun from "../components/chaxun.vue";
import Yaoqing from "../components/yaoqing.vue";

//接收我的页面暴露的组件
import Youhuiquan from "../components/youhuiquan.vue";
import Tuijianyoujiang from "../components/tuijianyoujiang.vue";
import Zhanghuanquan from "../components/zhanghuanquan.vue";
import Shenfen from "../components/shenfen.vue";

//登录注册暴露出来的组件
import Register from "../components/register.vue";
import Login from "../components/login.vue";
Vue.use(Router)
Vue.use(Mint)

export default new Router({
  routes: [
    //组件
    {
      path:'/main',
      component:Main,
      children:[
        {
          path:"/shouye",component:Shouye
        },
        {
          path:"/tuijian",component:Tuijian
        },
        {
          path:"/my",component:My
        },
        {
          path:"/gengduo",component:Gengduo
        }
      ]
    },

    //首页的组件路径
    {
      path:"/newer",component:Newer
    },
    {
      path:"/chanpin",component:Chanpin
    },
    {
      path:"/about",component:About
    },

    //推荐页面的组件路径
    {
      path:"/chaxun",component:Chaxun
    },
    {
      path:"/yaoqing",component:Yaoqing
    },

    //我的页面组件路径
    {
      path:"/youhuiquan",component:Youhuiquan
    },
    {
      path:"/tuijianyoujiang",component:Tuijianyoujiang
    },
    {
      path:"/zhanghuanquan",component:Zhanghuanquan
    },

    //登录注册
    {
      path:"/register",component:Register
    },
    {
      path:"/login",component:Login
    },
    {
      path:"/shenfen",component:Shenfen
    },
    {
      path:"/",redirect:"/shouye"
    }
  ]
})



