<template>
  <div>
    <header>
      <span class="fan iconfont icon-left" @click="tap()"></span>
      <p class="biaoti">注册</p>
      <p class="guan"></p>
    </header>

    <section>
      <div id="user">
        <p class="iconfont icon-lg-zh"></p><input class="num" type="text" v-model="phone" placeholder="请输入您的手机号">
      </div>

      <div id="yanzheng">
        <p class="iconfont icon-mima"></p><input type="text" v-model="code" placeholder="请输入短信验证码">
        <span @click="yanzhengma()">验证码</span>
      </div>

      <div id="pass">
        <p class="iconfont icon-mima"></p> <input name="password" type="password" v-model="pass" placeholder="请输入密码">
      </div>

      <div id="tongyi">
        <input type="radio" checked="checked"><p>同意</p><span>《客户服务协议》</span>
      </div>

      <button class="register" @click="zhuce()">注册</button>
    </section>

  </div>
</template>

<script>

  import axios from "axios";
  import $ from "jquery";
  import { MessageBox } from 'mint-ui';
    export default {
      name: "Register",
     data:function(){
       return{
         phone:'',
         pass:'',
         code:''
       }
     },
      methods:{
          tap(){
            this.$router.push("/login")
          },
        yanzhengma(){
              var _this = this;
              // console.log(_this.phone);
              axios({
                method:"post",
                url:"http://localhost:3000/register",
                params:{phonenum:_this.phone,id:1}
              }).then(function (data) {
                // console.log(data);
              })
        },
        zhuce(){
          var _this = this;
          axios({
            method:"post",
            url:"http://localhost:3000/register",
            params:{phonenum:_this.phone,password:_this.pass,code:_this.code}
          }).then(function (data) {
            // console.log(eval(data).data); jsonp格式数据转换
            if(data.data == 1){
              MessageBox({
                title: '恭喜！',
                message: '注册成功',
                showCancelButton: true
              });
              _this.$router.push("/login");
            }else{
              MessageBox({
                title: '注册失败！',
                message: '账号已存在或验证码不正确',
                showCancelButton: true
              });
            }
          })
        }
      }
    }
</script>

<style scoped>
  header{
    height:45px;
    line-height: 45px;
    background: #FFFFFF;
    font-size: 16px;
    color: red;
    z-index: 10;
    position: fixed;
    top: 0;
    width: 100vw;
    border-bottom: 1px solid #c0c0c0;
  }
  .fan{
    float: left;
    /*margin-left: 3%;*/
    font-size: 28px;
    line-height: 45px;
    text-align: center;
    width:15%;
  }
  .biaoti{
    text-align: center;
    float: left;
    width:70%;
    font-weight: bold;
  }
  .guan{
    float: left;
    /*margin-right:3%;*/
    text-align: center;
    font-size: 17px;
    width:15%;
  }
  section{
    flex:1;
    width: 100vw;
    height:100%;
    position: fixed;
    top: 45px;
    background: #f0f0f0;
  }
  #user{
    height:45px;
    width:80%;
    margin:20px auto 15px;
    background:#FFFFFF;
    border-radius: 45px;
    line-height: 45px;
  }
  #user p{
    float:left;
    height:45px;
    width:45px;
    text-align: center;
    color: red;
    font-size: 18px;
  }
  #user input{
    float:left;
    font-size: 16px;
    border: none;
    outline: none;
    line-height: 45px;
  }
  #yanzheng{
    height:45px;
    width:80%;
    margin:15px auto;
    background:#FFFFFF;
    border-radius: 45px;
    line-height: 45px;
  }
  #yanzheng p{
    float:left;
    height:45px;
    width:45px;
    text-align: center;
    color: red;
    font-size: 18px;
  }
  #yanzheng input{
    float:left;
    font-size: 16px;
    border: none;
    outline: none;
    line-height: 45px;
    width:60%;
  }
  #yanzheng span{
    display: block;
    float:left;
    font-size: 10px;
    color: red;
    text-decoration: underline;
  }
  #pass{
    height:45px;
    width:80%;
    margin:15px auto;
    background:#FFFFFF;
    border-radius: 45px;
    line-height: 45px;
  }
  #pass p{
    float:left;
    height:45px;
    width:45px;
    text-align: center;
    color: red;
    font-size: 18px;
  }
  #pass input{
    float:left;
    font-size: 16px;
    border: none;
    outline: none;
    line-height: 45px;
  }
  #tongyi{
    height:45px;
    width:80%;
    margin:10px auto;
    line-height: 45px;
  }
  #tongyi input{
    margin:17px 10px 0 20px;
    float:left;
  }
  #tongyi p{
    float:left;
  }
  #tongyi span{
    float:left;
    color: red;
  }
  .register{
    display: block;
    height:45px;
    width:80%;
    margin:15px auto;
    background:#b50d00;
    border-radius: 45px;
    line-height: 45px;
    color: #FFFFFF;
    outline: none;
  }
</style>



