<template>
  <div>
    <header>个人中心</header>
    <section>
      <div class="user">
        <span class="tou iconfont icon-wode"></span>
        <div>你好，<br/>{{name}}</div>
      </div>
      <div class="daohang">
        <div @click="youhuiquan()">
          <p class="iconfont icon-tiyanjin"></p>
          <span>优惠券</span>
        </div>
        <div @click="tap()">
          <p class="iconfont icon-tuijianyoujiang"></p>
          <span>奖金使用</span>
        </div>
        <div @click="tuijianyoujiang()">
          <p class="iconfont icon-tubiaolunkuohua09"></p>
          <span>推荐有奖</span>
        </div>
      </div>

      <ul>
        <li @click="shenfen()">
          <p class="qian iconfont icon-team"></p>
          <span>推荐好友</span>
          <p class="hou iconfont icon-right-circle"></p>
        </li>
        <li @click="zhanghuanquan()">
          <p class="qian iconfont icon-dkw_daikuan"></p>
          <span>账户</span>
          <p class="hou iconfont icon-right-circle"></p>
        </li>
        <li>
          <p class="qian iconfont icon-tubiaolunkuohua09"></p>
          <span>银行卡</span>
          <p class="hou iconfont icon-right-circle"></p>
        </li>
        <li>
          <p class="qian iconfont icon-gerenxinxi"></p>
          <span>个人信息管理</span>
          <p class="hou iconfont icon-right-circle"></p>
        </li>
        <li>
          <p class="qian iconfont icon-zhangdanjieqing"></p>
          <span>我的账单</span>
          <p class="hou iconfont icon-right-circle"></p>
        </li>
        <li class="tuichu" v-if='isshow' @click="tuichu()">
          <p class="qian iconfont icon-tuichu"></p>
          <span>退出登录</span>
          <p class="hou iconfont icon-right-circle"></p>
        </li>
      </ul>

      <div class="cheng"></div>
    </section>
    <div class="yin">
      <button class="a" @click="login()">登录</button>
      <button class="b" @click="register()">注册</button>
    </div>
  </div>

</template>

<script>
  import $ from "jquery";
  import { MessageBox } from 'mint-ui';
    export default {
        name: "My",
      data:function(){
        return{
          isshow:true,
          name:localStorage.getItem('num')
        }
      },
        methods:{
  youhuiquan(){
    // var data1 = JSON.parse(localStorage.getItem('hou'));
            if(this.name){
              this.$router.push("/youhuiquan")
            }else{
              $(function () {
                $(".yin").css("display","block");
                $(".yin").siblings().click(function () {
                  $(".yin").css("display","none");
                });
              })
            }
          },
          login(){
            this.$router.push("/login")
          },
          register(){
            this.$router.push("/register")
          },
          tuijianyoujiang(){
            if(this.name){
              this.$router.push("/tuijianyoujiang")
            }else{
              $(function () {
                $(".yin").css("display","block");
                $(".yin").siblings().click(function () {
                  $(".yin").css("display","none");
                });
              })
            }
          },
          zhanghuanquan(){
            if(this.name){
              this.$router.push("/chaxun")
            }else{
              $(function () {
                $(".yin").css("display","block");
                $(".yin").siblings().click(function () {
                  $(".yin").css("display","none");
                });
              })
            }
          },
            tap(){
              if(this.name){
                MessageBox('注意', '请先绑定身份信息');
              }else{
                $(function () {
                  $(".yin").css("display","block");
                  $(".yin").siblings().click(function () {
                    $(".yin").css("display","none");
                  });
                })
              }
            },
          tuichu(){
              if(this.name){
                this.isshow = false;
                localStorage.clear();
                this.name = "尊敬的会员";
                this.$router.go(0);
              }
          },
          shenfen(){
            if(this.name){
              this.$router.push("/shenfen")
            }else{
              $(function () {
                $(".yin").css("display","block");
                $(".yin").siblings().click(function () {
                  $(".yin").css("display","none");
                });
              })
            }

          }
        }
    }
</script>

<style scoped>
  .yin{
    position: fixed;
    bottom: 50px;
    background:#f0f0f0;
    z-index: 12;
    height:80px;
    width:100%;
    display: none;
  }
  .yin button{
    margin:0 auto;
    height:40px;
    width:100px;
    text-align: center;
    line-height: 40px;
    outline: none;
  }
  .yin .a{
    margin:20px 0 0 15%;
    background: #FFFFFF;
    color: #b50d00;
    border: 1px solid #b50d00;
  }
  .yin .b{
    float:right;
    margin:20px 15% 0 0;
    background: #b50d00;
    color: #FFFFFF;
    border: 1px solid #FFFFFF;
  }
  header{
    height:45px;
    width:100vw;
    text-align: center;
    background: #FFFFFF;
    line-height: 45px;
    font-size: 16px;
    color: red;
    font-weight: bold;
    position: fixed;
    z-index: 15;
  }
  section{
    position: absolute;
    top:45px;
    flex: 1;
    background: #f8f8f8;
  }
  .user{
    height:130px;
    width:100vw;
    background:#b50d00;
  }
  .tou{
    display: block;
    float:left;
    margin:15px 0 0 20px;
    height:50px;
    width:50px;
    text-align: center;
    font-size: 40px;
    color: #FFFFFF;
    line-height: 50px;
    border-radius: 50px;
    background:#ffa69d;
  }
  .user div{
    float:left;
    margin:20px 0 0 20px;
    color: #FFFFFF;
  }
  .daohang{
    height:96px;
    width:300px;
    background: #FFFFFF;
    margin:0 auto;
    position: relative;
    z-index: 10;
    top:-32px;
    border-radius:5px;
    box-shadow:0 2px 1px #ff7700;
  }
  .daohang div{
    text-align: center;
    width:33.33%;
    float: left;
    margin-top:10px;
  }
  .daohang p{
    height:50px;
    width:50px;
    color: red;
    font-size:30px;
    text-align: center;
    line-height: 50px;
    margin:0 auto;
  }
  .daohang span{
    font-size:12px;
  }
  ul{
    list-style: none;
  }
  li{
    height:60px;
    width:300px;
    background: #FFFFFF;
    margin:0 auto;
    margin-bottom: 2px;
    border-radius:8px;
  }
  li .qian{
    color:red;
    float:left;
    font-size:30px;
    line-height: 60px;
    margin-left:10px;
  }
  li span{
    display:block;
    float:left;
    margin-left:12px;
    line-height: 60px;
    font-size: 14px;
  }
  li .hou{
    float:right;
    font-size:22px;
    color: #f0f0f0;
    line-height: 60px;
    margin-right:10px;
  }
  .cheng{
    height:60px;
  }
</style>



