<template>
    <div>
      <header>
        <span class="fan iconfont icon-left" @click="tap()"></span>
        <p class="biaoti">新手指引</p>
        <p class="guan" @click="tap1()">关闭</p>
      </header>
      <section>
        <img src="static/newer1.jpg"/>
        <img src="static/newer2.jpg"/>
        <img src="static/newer3.jpg"/>
      </section>
    </div>
</template>

<script>
    export default {
        name: "Newer",
      methods:{
          tap(){
            this.$router.push("/shouye");
          },
        tap1(){
          this.$router.push("/shouye");
        }
      }
    }
</script>

<style scoped>
  *{
    margin:0;
    padding:0;
  }
  header{
    height:45px;
    width:100vw;
    line-height: 45px;
    font-size: 16px;
    color: red;
    background:#FFFFFF;
    position: fixed;
    z-index: 10;
  }
  .fan{
    float: left;
    font-size: 28px;
    line-height: 45px;
    text-align: center;
    width:15%;
  }
  .biaoti{
    text-align: center;
    float: left;
    width:70%;
    font-weight: bold;
  }
  .guan{
    float: left;
    /*margin-right:3%;*/
    text-align: center;
    font-size: 18px;
    width:15%;
  }
  section{
    flex: 1;
    background:#d02e1f;
    position: absolute;
    top:45px;
  }
  section img{
    margin:0;
    padding:0;
    display: block;
    width:100%;
    height:100%;
  }
</style>



