<template>
  <div>
    <header>
      <span class="fan"></span>
      <p class="biaoti">登录</p>
      <p class="guan"></p>
    </header>
    <section>
      <div class="dim" :style="{backgroundImage: 'url(' + img + ')' }" @click="gregister()">
        <span>X</span>
      </div>
      <div id="user">
        <p class="iconfont icon-lg-zh"></p><input type="text" v-model="num" placeholder="请输入您的手机号">
      </div>

      <div id="pass">
        <p class="iconfont icon-mima"></p> <input type="password" v-model="mima" placeholder="请输入密码">
      </div>

      <button class="login" @click="login1()">登录</button>

      <div class="gozhuce" @click="tap()">
        <span></span>
        <p>注册</p>
      </div>
    </section>
  </div>
</template>

<script>
  import axios from 'axios';
  import $ from 'jquery';
  import { MessageBox } from 'mint-ui';

  import {mapGetters,mapActions} from "vuex";

    export default {
        name: "Login",
      data:function(){
          return{
            num:'',
            mima:'',
            img:'static/logo.jpg'
          }
      },
      computed:mapGetters([]),
      methods:{
          tap(){
            this.$router.push("/register")
          },
          gregister(){
            this.$router.push("/register")
          },

          ...mapActions(["login"]),
          login1(){
              var _this = this;
            // this.login(this.num);
            axios({
              method:"post",
              url:"http://localhost:3000/login",
              params:{phonenum:_this.num,password:_this.mima}
            }).then(function (data) {
              if(data.data == 1){
                MessageBox({
                  title: '欢迎！',
                  message: '登陆成功',
                  showCancelButton: true
                });
                localStorage.setItem('num',_this.num);
                // var data1 = JSON.parse(localStorage.getItem('user'));
                // _this.num = data1;
                _this.$router.push("/shouye");
                // _this.login(_this.num);
              }else{
                console.log("账号或密码错误")
              }
            })
          }
      },

    }
</script>

<style scoped>
  header{
    height:45px;
    line-height: 45px;
    font-size: 16px;
    color: red;
    z-index: 10;
    position: fixed;
    width: 100vw;
    text-align: center;
  }
  .biaoti{
    text-align: center;
    float: left;
    width:100%;
    font-weight: bold;
  }
  section{
    flex:1;
    width: 100vw;
    height:100%;
    position: fixed;
    top: 45px;
    background: #f0f0f0;
  }
  .dim{
    height:22%;
    width:100%;
    background-repeat:no-repeat;
    background-position: center;
  }
  .dim span{
    float:right;
    margin-top:10px;
    margin-right:10px;
    font-size: 18px;
  }
  #user{
    height:45px;
    width:80%;
    margin:20px auto 15px;
    background:#FFFFFF;
    border-radius: 45px;
    line-height: 45px;
  }
  #user p{
    float:left;
    height:45px;
    width:45px;
    text-align: center;
    color: red;
    font-size: 18px;
  }
  #user input{
    float:left;
    font-size: 16px;
    border: none;
    outline: none;
    line-height: 45px;
  }
  #pass{
    height:45px;
    width:80%;
    margin:15px auto;
    background:#FFFFFF;
    border-radius: 45px;
    line-height: 45px;
  }
  #pass p{
    float:left;
    height:45px;
    width:45px;
    text-align: center;
    color: red;
    font-size: 18px;
  }
  #pass input{
    float:left;
    font-size: 16px;
    border: none;
    outline: none;
    line-height: 45px;
  }
  .login{
    display: block;
    height:45px;
    width:88%;
    margin:25px auto;
    background:#b50d00;
    border-radius: 45px;
    line-height: 45px;
    color: #FFFFFF;
    outline: none;
  }
  .gozhuce{
    margin-left:12%;
    height:45px;
  }
  .gozhuce span{
    display: block;
    float:left;
    border-left: 10px solid #f0f0f0;
    border-top: 5px solid #f0f0f0;
    border-bottom: 5px solid #f0f0f0;
    border-right: 10px solid #b50d00;
    margin-top:5px;
  }
  .gozhuce p{
    float:left;
    margin-left:5px;
    font-size: 14px;
    color: #b50d00;
  }
</style>



