import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

// 加载模块并暴露出去
export default new Vuex.Store({
  state:{       //相当于组件data的作用
    name:'尊敬的会员'
  },
  mutations:{     //相当于组件methods的作用
    login(a,b){
      a.name = b;
      console.log("aaaa")
    }
  },
  actions:{
    login:({commit},b)=>commit("login",b)
  }

})



