<template>
  <div>
    <header>
      <span class="fan iconfont icon-left" @click="tap()"></span>
      <p class="biaoti">我的优惠券</p>
      <p class="guan"></p>
    </header>
    <div class="du">
      <ul class="lists">
        <li class="a cc">未使用</li>
        <li class="b">已使用</li>
        <li class="c">已过期</li>
      </ul>
    </div>

  </div>

</template>

<script>
  import $ from "jquery";

    export default {
        name: "Youhuiquan",
        methods:{
          tap(){
            this.$router.push("/my")
          }
        }
    }
    $(function () {
      $("li").click(function () {
        $(this).addClass("cc").siblings("li").removeClass("cc");
      });
    });

</script>

<style scoped>
  header{
    height:45px;
    line-height: 45px;
    font-size: 16px;
    color: red;
    z-index: 10;
    position: fixed;
    width: 100vw;
  }
  .fan{
    float: left;
    /*margin-left: 3%;*/
    font-size: 28px;
    line-height: 45px;
    text-align: center;
    width:15%;
  }
  .biaoti{
    text-align: center;
    float: left;
    width:70%;
    font-weight: bold;
  }
  .guan{
    float: left;
    /*margin-right:3%;*/
    text-align: center;
    font-size: 17px;
    width:15%;
  }
  .du{
    position: absolute;
    top:45px;
    background: #b50d00;
    height:45px;
    width:100vw;
  }
  .lists{
    width:84%;
    margin:0 auto;
    margin-top: 9px;
    list-style: none;
    border-radius: 5px;
  }
  ul li{
    height:27px;
    width: 32.5%;
    text-align: center;
    line-height: 27px;
    border:1px solid #FFFFFF;
    float:left;
    margin:0 auto;
    color: #FFFFFF;
    font-size: 14px;
  }
  .cc{
    background: #FFFFFF;
    color: #b50d00;
  }
</style>



